import React from 'react'
import './Toolbar.css'
import {Avatar,IconButton} from "@material-ui/core";
import ListAltOutlinedIcon from '@mui/icons-material/ListAltOutlined';
import CalendarMonthOutlinedIcon from '@mui/icons-material/CalendarMonthOutlined';
import AttachFileIcon from '@mui/icons-material/AttachFile';
import { Link } from 'react-router-dom';
import { useDispCal,useC,useChaka} from './Helper';

function Toolbar() {
  const {Other,setOther,ShowOther,RemoveOther}=useDispCal();
  return (
    <div className='toolbar' id='tool'>
      <Link to='/upload'>
      <IconButton style={{background:'#2A3166',color:'#EE7879',marginBottom:'10px'}} >
    <AttachFileIcon style={{height: '35px', width:'35px'}} />
    </IconButton>
    </Link>
    <Link to='/cal'>
      <IconButton style={{background:'#2A3166',color:'#EE7879',marginBottom:'10px'}} >
      <CalendarMonthOutlinedIcon style={{height: '35px', width:'35px'}}/>
      </IconButton>
      </Link>
     
      <IconButton style={{background:'#2A3166',color:'#EE7879',marginBottom:'10px'}}>
      <ListAltOutlinedIcon style={{height: '35px', width:'35px'}}/>
      </IconButton>
      
    </div>
    
  )
}

export default Toolbar
