import React ,{useState,useEffect}from 'react'
import './Login.css'
import { useSingleuser,useYou } from './Helper';
import {Link} from "react-router-dom";
import axios from 'axios';
function Login(props) {
    const {you,setYou}=useYou();
    const [user,setUser]=useState('');
    const [updated,setUpdated] =useState('')
    const [lock,setLock]=useState(false);
    const {singleuser,setSingleuser}=useSingleuser();
   

    function opplock(){
      if(lock===false){
        setLock(true);
      }
      else{
        setLock(false);
      }
    }
    const take=(e)=>{
      e.preventDefault();
      console.log("The User is : "+you)
      setYou(user)
      props.getfr();
    }
    const Sendname = (e) => {
      setYou(e.target.value);
      props.onSPropValueChange(updated);
      setUpdated(e.target.value);
      };
    const Showsign=()=>{
        opplock();
        if(lock===true){
          document.getElementById('sign_up_show').innerHTML="Have an account ? Login"
          document.getElementById('s').style.display='flex';
          document.getElementById('login_submit').disabled=true
          document.getElementById('login_name').disabled=true
        }
        else{
          document.getElementById('sign_up_show').innerHTML="No account? Sign Up"
          document.getElementById('s').style.display='none';
          document.getElementById('login_submit').disabled=false
          document.getElementById('login_name').disabled=false
          
        }
    }
    const load=()=>{
      props.ola(you)
      props.getfr();
    }
    useEffect(()=>{
      props.ola(user)
    },[user])

 

    // This is the sign in ka part

    const [formData, setFormData] = useState({});

  const handleSubmit = (event) => {
    event.preventDefault();
    const name = event.target.elements.name.value;
    const numbers = event.target.elements.numbers.value;
    const link = event.target.elements.link.value;
    const interest = event.target.elements.interest.value;
    const data = {
      name,
      numbers,
      link,
      interest
    };
    const jsonData = JSON.stringify(data);
    console.log(data)
    
    setFormData(data);
    console.log("It is was now")

  };
  function putData() {
    try {
      console.log("It is here now")
      axios.post('http://localhost:8021/users' ,formData );
     
    } catch (error) {
      console.log(error);
    }
  }
 
  console.log("It over")
  useEffect(()=>{
    putData();
  },[formData])
    


  


  return (
    <div className='login'>
        <div className="wrap">
      <div className="box">
       <h1 id='login_title'>Login</h1> 
      
       <form className='logs' onSubmit={take} >
        <input type="text" placeholder='Enter your Name' id='login_name' value={user} onChange={(event) =>
          setUser(event.target.value)
        }/>
        
        <button type="submit"id="login_submit"   onClick={take}>Register Name</button>
       </form>
        <Link to='/chats'>
        <button type="button"id="after_go" onClick={load}>Go to Chats</button>
        </Link>

        <button type="button"id="sign_up_show" onClick={Showsign}>No account? Sign Up</button>
      </div>
       <div className="sign_up" id='s'>
      
        <form onSubmit={handleSubmit} id='sign' className='logs'>
      <input type="text" name="name"  placeholder='Create username'/>
     
      <input type="number" name="numbers" placeholder='Enter number'/>
      <input type="text" name="link" placeholder='Enter DP link'/>
      <input type="text" name="interest" placeholder='Enter you Aim/Interest' />
      <button type="submit" id="sign_submit">Submit</button>
    </form>
       </div>
       </div>
    </div>
  )
}

export default Login
