import { useState } from 'react';
import './Header.css'
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import AttachFileIcon from '@mui/icons-material/AttachFile';
import CloseIcon from '@mui/icons-material/Close';import {Avatar,IconButton} from "@material-ui/core";
import { Link } from 'react-router-dom';
import LogoutOutlinedIcon from '@mui/icons-material/LogoutOutlined';
import MessageOutlinedIcon from '@mui/icons-material/MessageOutlined';
import {useSingleuser,useYou,useChats,useSide,useUpdateside,useUserdetail,useUSerdetaildisplay,useOtherdetail} from './Helper'
import './People.css'
function Header(props) {
  const User=useUserdetail();
  const{you,setYou}=useYou();
  const {singleuser,setSingleuser}=useSingleuser();
  const {ShowUser,RemoveUser}=useUSerdetaildisplay();
  const {Other,setOther,ShowOther,RemoveOther}=useOtherdetail();
  const show=useUpdateside();
  const Vis=useSide();
  const {chats,setChats}=useChats();
  
  

  return (
    <>
    
    <div className='header'>
        <div className="user_info" onMouseOver={ShowUser} onMouseOut={RemoveUser}>
            {props.val.map((mem)=>(
            <div className="logo">
            <Avatar src={mem.link} style={{ height: '60px', width: '60px' }}/>
            </div>
             ))}
            <div className="name">
            <h2>{you}</h2>
            </div>
            
        </div>
       
        <div className="contact_info">
            <div className="contacts" onMouseOver={ShowOther} onMouseOut={RemoveOther}>
            {props.elem.map((mem)=>(
            <div className="contact_logo">
            <Avatar src={mem.link} style={{ height: '60px', width: '60px' }} />
            </div>
              ))} 
            <div className="contact_name">
           <h2>{chats}</h2>
            </div>
            </div>
        </div>
            <div className="header_buttons">
               <div className="innerbut">
               {Vis ?
                <IconButton style={{backgroundColor:'white',color
                :'#2A3166'}} onClick={show} >
                <CloseIcon/>
                </IconButton>: 
                <IconButton style={{backgroundColor:'white',color:'#2A3166'}} onClick={show} >
                <MenuIcon/>
                </IconButton>}
                <IconButton style={{backgroundColor:'white',color
  :'#2A3166'}} onClick={props.myCare}>
    <MessageOutlinedIcon style={{backgroundColor:'white',color
  :'#2A3166'}}/>
    </IconButton>
    
    <Link to='/'>
    <IconButton style={{backgroundColor:'white',color
  :'#2A3166'}}>
    <LogoutOutlinedIcon/>
    </IconButton>
    </Link>
    </div>
            </div>
    </div>

    </>
  )
}

export default Header
