import React from 'react'
import {Avatar,IconButton} from "@material-ui/core";
import './People.css'
import {useChats} from './Helper'
function Indivisual({members}) {
  
  return (
    <>
    {members.map((member)=>(
    <div className='indivisual'>
      <div className="person">
        <div className="perlogo">
      <Avatar src={member.link} style={{ height: '60px', width: '60px',marginTop:'0px' }}/>
      </div>
      <div className="pername">
      <h3>{member.name}</h3>
      <p>{member.interest}</p>
      </div>
      </div>
    </div>))}
    </>
  )
}

export default Indivisual
